.gandalf {
	position: relative;
	width: 400px;
	height: 400px;
	margin: 0 auto;
	top: 1rem;
	-webkit-animation: floating 1s infinite alternate ease-in-out;
	        animation: floating 1s infinite alternate ease-in-out;
}

.gandalf div {
	position: absolute;
}

.gandalf::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 400px;
	height: 400px;
	background-color: #1a2130;
	border-radius: 50%;
}

.gandalf .fireball {
	bottom: -10px;
	left: 50px;
	width: 300px;
	height: 100px;
	border-radius: 50%;
	background: radial-gradient(#efac41, #de8531, #6c1305, black);
	border: 5px solid #000;
}

.gandalf .skirt {
	bottom: 50px;
	left: 100px;
	border-bottom: 230px solid #ededed;
	border-left: 100px solid transparent;
	border-right: 100px solid transparent;
	-webkit-filter: drop-shadow(0 0 6px lightgray);
	        filter: drop-shadow(0 0 6px lightgray);
}

.gandalf .skirt::before {
	content: "";
	position: absolute;
	background-color: #ededed;
	width: 100px;
	height: 21px;
	top: 230px;
	left: 0px;
	border-bottom-right-radius: 180%;
	border-bottom-left-radius: 100%;
}

.gandalf .skirt::after {
	content: "";
	position: absolute;
	background-color: #ededed;
	width: 100px;
	height: 28px;
	top: 230px;
	left: -100px;
	border-bottom-right-radius: 80%;
	border-bottom-left-radius: 180%;
}

.gandalf .sleeves::before,
.gandalf .sleeves::after {
	content: "";
	position: absolute;
	border-bottom: 70px solid #ededed;
	-webkit-filter: drop-shadow(0 0 6px lightgray);
	        filter: drop-shadow(0 0 6px lightgray);
}

.gandalf .sleeves::before {
	top: 130px;
	left: 191px;
	border-left: 100px solid transparent;
	border-right: 40px solid transparent;
	-webkit-transform: rotate(-34deg);
	        transform: rotate(-34deg);
}

.gandalf .sleeves::after {
	top: 127px;
	left: 70px;
	border-left: 40px solid transparent;
	border-right: 100px solid transparent;
	-webkit-transform: rotate(41deg);
	        transform: rotate(41deg);
}

.gandalf .shoulders {
	background-color: #ededed;
	border-radius: 50%;
	width: 100px;
	height: 130px;
	left: 150px;
	top: 120px;
}

.gandalf .shoulders .hand {
	width: 33px;
	height: 26px;
	border-radius: 50%;
	background-color: #ffd8ad;
	top: -6px;
}

.gandalf .shoulders .left {
	left: -70px;
	-webkit-transform: rotate(-20deg);
	        transform: rotate(-20deg);
}

.gandalf .shoulders .left::after {
	content: "";
	position: absolute;
	background-color: #e6e6e6;
	width: 126px;
	height: 8px;
	border-radius: 4px;
	-webkit-transform: rotate(-105deg);
	        transform: rotate(-105deg);
	-webkit-transform-origin: bottom;
	        transform-origin: bottom;
	top: -48px;
	left: -56px;
}

.gandalf .shoulders .right {
	right: -70px;
	-webkit-transform: rotate(20deg);
	        transform: rotate(20deg);
}

.gandalf .shoulders .right::after {
	content: "";
	position: absolute;
	background-color: #bf5507;
	width: 250px;
	height: 5px;
	border-radius: 2.5px;
	-webkit-transform: rotate(-78deg);
	        transform: rotate(-78deg);
	-webkit-transform-origin: left;
	        transform-origin: left;
	bottom: -100px;
	left: 0;
}

.gandalf .head {
	width: 80px;
	height: 90px;
	top: 80px;
	left: 160px;
	background-color: #ffd8ad;
	border-radius: 50%;
}

.gandalf .head::before,
.gandalf .head::after {
	content: "";
	position: absolute;
	background-color: #000;
